@import "./variables";

@font-face {
  font-family: 'Proxima Nova';
  src: url("/wp-content/themes/grodfood/resources/assets/fonts/ProximaNova-Light.ttf") format('truetype');
  src: url("/wp-content/themes/grodfood/resources/assets/fonts/ProximaNova-Regular.ttf") format('truetype');
}

@font-face {
  font-family: 'Merriweather';
  src: url("/wp-content/themes/grodfood/resources/assets/fonts/Merriweather-Regular.ttf") format('truetype');
  src: url("/wp-content/themes/grodfood/resources/assets/fonts/Merriweather-Bold.ttf") format('truetype');
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  top: 0 !important;
}

.main {
  height: 100%;
}



